import { FC } from "react";
import { Button, HStack, IconButton, VStack, Text, useColorModeValue, Stack } from "@chakra-ui/react";

import { configs } from "shared/content/Content";
import { onResumeOpen, open } from "utils/Functions";
import { FacebookIcon, GitHubIcon, InstagramIcon, LinkedInIcon, MailIcon, YoutubeIcon, ScholarIcon, CVIcon } from "utils/Icons";

const LinksToIconMapper: Record<string, JSX.Element> = {
    linkedin: <LinkedInIcon />,
    github: <GitHubIcon />,
    facebook: <FacebookIcon />,
    instagram: <InstagramIcon />,
    youtube: <YoutubeIcon />,
    mail: <MailIcon />,
    google_scholar: <ScholarIcon />,
    curriculum_vitae: <CVIcon />
};

interface Props {
    resume?: boolean;
    exclude?: Array<string>;
    delay?: number;
}

export const Socials: FC<Props> = ({ resume = true, exclude, delay = 800 }) => {
    const textColor = useColorModeValue("black.600", "black.300");

    return (
        /* ⭐ 1. Stack의 방향을 반응형으로 설정: 모바일(base)은 가로(row), 데스크탑(md)은 세로(column) */
        <Stack 
            direction={{ base: "row", md: "column" }} 
            align={{ base: "center", md: "start" }}
            spacing={{ base: 0, md: 0 }} // 모바일일 때 아이콘 간격 조정
            wrap="wrap" // 모바일에서 아이콘이 너무 많을 경우 줄바꿈 허용
        >
            {resume && (
                <Button 
                    data-aos="fade" 
                    data-aos-delay={delay} 
                    size="md" 
                    borderRadius="xl" 
                    onClick={onResumeOpen}
                    display={{ base: "none", md: "inline-flex" }} // 모바일에서 버튼이 너무 크면 숨기거나 작게 조정 가능
                >
                    Resume
                </Button>
            )}

            {configs.common.socials.map(
                (social) =>
                    !exclude?.includes(social.type) && (
                        <HStack
                            key={social.type}
                            as="button"
                            onClick={() => open(social.link)}
                            aria-label={`${social.type}-button`}
                            spacing={{ base: 0, md: 3 }} // 모바일에서는 텍스트가 없으므로 간격 0
                        >
                            <IconButton
                                boxSize={{ base: "30px", md: "13px" }} // 모바일에서 터치하기 쉽게 크기 살짝 키움
                                p="0"
                                variant="icon"
                                aria-label={`${social.type}-icon`}
                                icon={LinksToIconMapper[social.type]}
                                fontSize={social.type === "mail" ? "16pt" : "14pt"}
                            />
                            
                            {/* ⭐ 2. 모바일(base)에서는 텍스트 숨김, 데스크탑(md)에서만 표시 */}
                            <Text 
                                display={{ base: "none", md: "block" }}
                                fontSize="14" 
                                fontWeight="700" 
                                color={textColor} 
                                textTransform="capitalize"
                            >
                                {social.type === "curriculum_vitae" ? "CV (PDF)" : social.type.replace("_", " ")}
                            </Text>
                        </HStack>
                    )
            )}
        </Stack>
    );
};