import { FC, useState, Fragment } from "react";
import {
  Text,
  Grid,
  GridItem,
  useColorModeValue,
  Box,
  Button,
  Flex,
  Divider
} from "@chakra-ui/react";
import { HighlightTag } from "shared/tags/HighlightTag";

export const Updates: FC = () => {
  const textColor = useColorModeValue("gray.700", "gray.200");
  const yearColor = useColorModeValue("primary.700", "primary.200");
  const [expanded, setExpanded] = useState(false);

  const groupedUpdates = [
    {
      year: "2026",
      items: [
        {
          date: "Jan.16",
          text: <>A paper I co-authored titled <Text as="span" fontWeight="600" >"Understanding Gaze-Based Identification in VR Through Preattentive Processing and Binocular Rivalry"</Text> was accepted to <HighlightTag color="green">CHI</HighlightTag></>,
        }
      ]

    },
    {
      year: "2025",
      items: [
       
        {
          date: "Sep.28",
          text: <>First time in <HighlightTag color="purple">UIST</HighlightTag> conference in Busan</>,
        },
        {
          date: "Aug.29",
          text: <>Awarded <HighlightTag color="gray">the NRF's 2025 Research Grant</HighlightTag> for Ph.D. (KRW 25 million, ≈ $18,000 USD) in Science and Engineering Track</>,
        },
        {
          date: "Jan.17",
          text: <>Our paper <Text as="span" fontWeight="600" >"BIASsist: Empowering News Readers via Bias Identification, Explanation, and Neutralization"</Text> was accepted to <HighlightTag color="green">CHI</HighlightTag></>,
        },
        {
          date: "Jan.13",
          text: <>A paper I co-authored titled <Text as="span" fontWeight="600" >"Pre-AttentiveGaze: gaze-based authentication dataset with momentary visual interactions"</Text> was accepted to <HighlightTag color="green">Scientific Data</HighlightTag></>,
        },
      ],
    },
    {
      year: "2024",
      items: [
        { date: "Oct.05", text: <>Our paper titled <Text as="span" fontWeight="600" >"Know Me Inside-Out: Conversational and Quiz-Based System for Reminiscence Therapy for People with Dementia"</Text> was accepted at <HighlightTag color="green"> UbiComp Companion</HighlightTag></> },
        { date: "May.10", text: <>Joined the <HighlightTag color="purple">CHI</HighlightTag> conference in Hawaii</> },
        { date: "Jan.15", text: <>A paper I co-authored <Text as="span" fontWeight="600" >"A Way for the Deaf and Hard of Hearing People to Enjoy Music by Exploring and Customizing Cross-modal Music Concepts"</Text> was accepted to <HighlightTag color="green">CHI</HighlightTag></> },
      ],
    },
    {
      year: "2023",
      items: [
        { date: "Oct.15", text: <>Visited the <HighlightTag color="blue">Toronto Univ</HighlightTag> and introduced my projects</> },
        { date: "Oct.01", text: <>Visited the <HighlightTag color="blue">MIT</HighlightTag> in Boston and met with collaborators</> },
        { date: "Apr.22", text: <>Attended the <HighlightTag color="purple">CHI</HighlightTag> conference in Hamberg</> },
      ],
    },
    {
      year: "2022",
      items: [
        { date: "Sep.01", text: <>Started my <HighlightTag color="gray">Ph.D journey</HighlightTag> in Human-Computer Interaction.</> },
      ],
    },
    {
      year: "2019",
      items: [
        { date: "Feb", text: <>Took part in <HighlightTag color="purple">DeveloperWeek</HighlightTag> held in Oakland</> },
      ],
    },
    {
      year: "2018",
      items: [
        { date: "Dec", text: <>Started <HighlightTag color="blue">Internship</HighlightTag> in Silicon Valley</> },
      ],
    },
  ];

  // 처음에 2개 그룹만 보여줌
  const visibleGroups = expanded ? groupedUpdates : groupedUpdates.slice(0, 2);

  return (
    <Box>
      <Grid 
        templateColumns="repeat(10, 1fr)" 
        gap={{ base: 2, md: 1 }} 
        rowGap={{ base: 3, md: 1 }}
      >
        {visibleGroups.map((group, groupIndex) => (
          <Fragment key={group.year}>
            {/* 연도: 모바일에서는 10칸 전체 차지(위쪽), 데스크탑에서는 1칸(왼쪽) */}
            <GridItem 
              colSpan={{ base: 10, md: 1 }} 
              rowSpan={{ base: 1, md: group.items.length }}
              mb={{ base: -1, md: 0 }}
            >
              <Text color={yearColor} fontSize="16px" fontWeight="700">
                {group.year}
              </Text>
            </GridItem>

            {group.items.map((update, itemIndex) => (
              <Fragment key={itemIndex}>
                {/* 1. 날짜 (Date) 
                   - base: 10 (한 줄 전체 차지) -> 줄바꿈 효과
                   - md: 1 (데스크탑은 1칸)
                   - pl: 0 (왼쪽 정렬 맞춤)
                   - mb: 모바일에서 날짜와 내용 사이 간격을 좁히고 싶으면 -1 or -2 정도 줄 수 있음
                */}
                <GridItem 
                    colSpan={{ base: 10, md: 1 }} 
                    pl={0}
                    mb={{ base: -4, md: 0 }} // 모바일에서 날짜와 내용 사이를 좀 더 붙임 (선택사항)
                >
                  <Text fontSize="16" fontWeight="semibold">
                    {update.date}
                  </Text>
                </GridItem>

                {/* 2. 내용 (Text)
                   - base: 10 (한 줄 전체 차지) -> 날짜 밑으로 내려옴
                   - md: 8 (데스크탑은 8칸)
                   - pl: 0 (왼쪽 정렬 맞춤)
                */}
                <GridItem colSpan={{ base: 10, md: 8 }} pl={0}>
                  <Text as="span" fontSize="16">{update.text}</Text>
                </GridItem>
              </Fragment>
            ))}
            
            {/* 구분선 */}
            {groupIndex < visibleGroups.length - 1 && (
              <GridItem colSpan={10} py={2}>
                <Divider />
              </GridItem>
            )}
          </Fragment>
        ))}
      </Grid>

      {groupedUpdates.length > 2 && (
        <Flex justify="flex-end" mt={4}>
          <Button
            size="sm"
            variant="link"
            fontWeight={500}
            color={textColor}
            onClick={() => setExpanded(!expanded)}
          >
            {expanded ? "Show less" : "Show more"}
          </Button>
        </Flex>
      )}
    </Box>
  );
};